const elapsedTime = (date: string) => {
  const start = new Date(date);
  const end = new Date();
  const diff = end.getTime() - start.getTime();
  const times = [
    { time: "", milliSeconds: 1000 * 60 * 60 * 24 * 365 },
    { time: "", milliSeconds: 1000 * 60 * 60 * 24 * 30 },
    { time: "", milliSeconds: 1000 * 60 * 60 * 24 },
    { time: "", milliSeconds: 1000 * 60 * 60 },
    { time: "", milliSeconds: 1000 * 60 }
  ];

  for (let i = 0; i < times.length; ) {
    const betweenTime = Math.floor(diff / times[i].milliSeconds);
    if (betweenTime > 0) {
      return `${betweenTime}${times[i].time} `;
    }
    i += 1;
  }
  return " ";
};

export default elapsedTime;
